/* ------------------------------------------- */
/* BANNER SECTION */
/* ------------------------------------------- */
.banner {

    .swiper-container {
        overflow: hidden;
        position: relative;
        z-index: 0;
    }

    .swiper-slide {
        position: relative;
        max-width: 1920px;
        min-height: 350px;
        height: calc(100vh - 260px);
        display: flex;

        &::before {
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 0;
            background-color: rgba(20, 33, 51, 0.7);
        }

        img {
            width: 100%;
        }

        &__content {
            position: absolute;
            display: flex;
            justify-content: center;
            left: 0;
            right: 0;
            top: 36%;
            z-index: 0;
            
            .banner__headers {
                max-width: 1140px;
                text-align: center;
                position: relative;

                @include extra-large-size-max {
                    max-width: 1000px;
                    font-size: 60px;
                }
                
                @include extra-large-size-medium {
                    max-width: 860px;
                    font-size: 54px;
                }

                @include large-size-max {
                    max-width: 700px;
                    font-size: 46px;
                }

                @include small-size-max {
                    max-width: 500px;
                    font-size: 36px;
                    padding: 0 10px;
                }

                @include extra-small-phone-size-max {
                    max-width: 320px;
                    padding: 20px;
                }
            }

            span {
                display: inline-block;
                position: relative;
                z-index: 1;
            }

            @include extra-large-size-medium {
                top: 37%;
            }

            @include large-size-max {
                top: 39%;
            }

            @include medium-size-max {
                top: 35%;
            }

            @include extra-small-phone-size-max {
                top: 15%;
            }
        }

        @include extra-large-size-medium {
            height: calc(100vh - 250px);
        }

        @include large-size-max {
            height: calc(100vh - 224px);
        }

        @media screen and (max-width: 1024px) {
            height: 520px;
        }

        @include medium-size-max {
            height: 380px;
        }

        @include extra-small-size-max {
            height: 280px;
        }
    }
    
    .swiper-button {
        top: calc(50% - 50px);
        width: 67px;
        height: 60px;
        font-style: normal;
        z-index: 6;
        
        &::after {
            font-size: 26px;
            color: $c-white;
        }
        
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background-color: $c-white;
            opacity: 0.1;
            transform: skewX(-7deg);
            transition: opacity 250ms ease-in;
            z-index: 1;
        }
    
        &:hover {
            &::after {
                color: $c-pomegranate;
                z-index: 4;
            }
    
            &::before {
                opacity: 1;
                z-index: 4;
            }
        }
    }

    .swiper-pagination {
        bottom: 50px;

        span {
            width: 10px;
            height: 9px;
            border-radius: 0;
            transform: skew(-7deg);
            background-color: $c-white;
        }

        .swiper-pagination-bullet-active {
            width: 20px;
            height: 18px;
            box-shadow: 2px 2px 0 rgba(45, 63, 89, 0.1);
            background-color: rgba($color: $c-white, $alpha: .8);
        }

        @include extra-small-size-max {
            bottom: 40px;
        }
    }
    
    .swiper-button-prev {
        left: 80px;
        z-index: 3;

        @media screen and (max-width: 1024px) {
            display: none;
        }
    }
    
    .swiper-button-next {
        right: 80px;
        z-index: 3;

        @media screen and (max-width: 1024px) {
            display: none;
        }
    }
}