$button-light-color: $c-white;

// CONTENT FONT
$content-font-family: $f-roboto-slab;
$content-txt-size: 15px;
$content-font-weight: 400;
$content-line-height: 26px;
$content-txt-color: $c-white;

// BUTTONS FONT
