/* ------------------------------------------- */
/* INFO SECTION */
/* ------------------------------------------- */
.info-section {
    background-color: $c-porcelain;
    padding-top: 85px;

    &__content {
        max-width: 850px;
        width: 100%;
        margin: 0 auto;

        &-title {
            color: $c-blue-dark;
            margin-bottom: 20px;
        }

        &-text {
            font-family: $f-roboto-slab;
            color: $c-grey;
            font-size: 18px;
            line-height: 28px;

            @include small-size-max {
                font-size: 15px;
            }
        }

        @include medium-size-max {
            padding: 0 10px;
        }

        @include small-size-max {
            padding: 0;
        }
    }

    @include small-size-max {
        padding-top: 85px;
    }

    @include extra-small-size-max {
        padding-top: 50px;
    }
}