/* ------------------------------------------- */
/* SEARCH SECTION */
/* ------------------------------------------- */
.rating-section {
    background-color: $c-porcelain;

    &__content {
        display: flex;
        justify-content: space-around;
        
        &-left {
            
            &__title {
                color: $c-blue-dark;
                margin-bottom: 20px;

                @include small-size-max {
                    text-align: center;
                    font-size: 30px;
                    line-height: 34px;
                }
            }

            .rating {
                position: relative;
                display: flex;
                margin: 0 auto;
                height: 74px;
                width: fit-content;

                &::before {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    z-index: 0;
                    transform: skewX(-6deg);
                    outline: 1px solid transparent;
                    background-color: rgba($color: $c-blue-main, $alpha: .1);
                }

                &__text {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 74px;
                    color: $c-white;
                    font-size: 40px;
                    font-weight: 500;
                    line-height: 60px;
                    padding: 0 18px;
                    z-index: 1;

                    &::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        left: 0;
                        top: 0;
                        background-color: $c-pomegranate;
                        transform: skewX(-6deg);
                        outline: 1px solid transparent;
                        z-index: -1;
                    }

                    @include extra-small-size-max {
                        height: 60px;
                        font-size: 32px;
                    }
                }
                
                &__stars-wrapper {
                    display: flex;
                    width: 100%;
                    height: 74px;
                    padding: 0 25px;

                    @include extra-small-size-max {
                        height: 60px;
                        padding: 0 10px 0 17px;
                        font-size: 34px;
                    }
                }

                &__stars {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    &-star {
                        position: relative;

                        i {
                            font-size: 36px;
                            color: rgba($color: $c-white, $alpha: .8);
                            margin: 0 4px;

                            &::before {
                                content: '\f005';
                            }

                            @include extra-small-size-max {
                                font-size: 26px;
                            }
                        }

                        &__fill {
                            z-index: 2;
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            bottom: 0;
                            overflow: hidden;

                            i {
                               color: $c-pomegranate;
                            }
                        }
                    }
                }

                @include extra-small-size-max {
                    height: 60px;
                }
            }

            @include large-size-max {
                margin-bottom: 40px;
            }
        }
        
        &-right {
            max-width: 570px;
            
            &__text {
                color: $c-blue-dark;

                @include small-size-max {
                    text-align: center;
                    font-size: 30px;
                    line-height: 34px;
                }
            }
        }

        @include large-size-max {
            flex-direction: column;
            align-items: center;
        }

        @include extra-small-size-max {
            padding: 0 10px;
        }
    }
}