/* ------------------------------------------- */
/* EMAIL POP-UP */
/* ------------------------------------------- */
.email-popup__trigger {
    position: fixed;
    z-index: 1111;
    right: 0;
    bottom: 10%;
    transform-origin: 100% 100%;
    transform: rotate(-90deg) translate(100%, 0);
    font-size: 16px;
    font-weight: 600;
    padding: 8px 15px;
    min-width: auto;

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: $c-pomegranate;
        transform: skewX(-6deg);
        outline: 1px solid transparent;
        transition: background-color 250ms ease-in;
        border: 2px solid $c-blue-light;
        border-bottom: none;
        z-index: -1;
    }

    &:hover {

        &::before {
            background-color: #ff4d53;
        }
    }

    i {
        margin-right: 5px;
        font-size: 18px;
    }
}

.email-popup {
    visibility: hidden;
    right: inherit;
    position: fixed;
    right: -800px;
    bottom: 5px;
    display: block;
    width: 380px;
    height: 470px;
    background-color: $c-white-grey;
    z-index: 2222;
    box-shadow: 0 0 0.688em 0 rgba(0, 0, 0, .5);
    transition: all .5s ease-out;

    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        height: 100%;
    }

    &__top {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 15px;
        max-height: 45px;
        background-color: #3498db;
        font-family: 'Lato', sans-serif;
        font-size: 18px;

        &-btn {
            width: 20px;
            height: 20px;
            background-color: transparent;
            outline: none;
            border: none;
            padding: 0;
            cursor: pointer;
        }

        svg {
            height: 28px;
            width: 28px;
            pointer-events: none;

            &:last-child {
                width: 20px;
                height: 20px;
            }
        }
    }


    &__main {
        width: 100%;
        height: calc(100% - 100px);
        overflow-y: auto;
        padding: 10px 20px;

        &-form {
            font-family: 'Lato', sans-serif;


            input,
            select,
            textarea {
                width: 100%;
                height: 40px;
                margin: 3px 0;
                padding: 7px;
                font-size: 14px;
                color: #666666;
                font-weight: 700;
                background-color: #ffffff;
                border-radius: 5px;
                border: 1px solid #dddddd;
                overflow: auto;
                outline: none;

                &::placeholder {
                    font-size: 16px;
                    opacity: 0.7;
                }
            }

            textarea {
                height: 70px;
                resize: none;
            }

            label {
                display: inline-block;
                margin: 5px 0 0 2px;
                font-weight: 700;
                font-size: 12px;
                color: #666666;
                line-height: normal;

                span {
                    color: #ce462c;
                    padding-left: 4px;
                    font-size: 14px;
                }
            }

            *::selection {
                color: black;
            }
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background-color: rgba($color: $c-black, $alpha: .15);
            box-shadow: initial;
        }
        
        &::-webkit-scrollbar {
            width: 10px;
        }
        
        &::-webkit-scrollbar-track {
            border-radius: 0;
            box-shadow: inset 0 0 6px rgba($color: $c-black, $alpha: .1);
        }
    }

    &__bottom {
        width: 100%;
        height: 60px;
        padding: 0 15px 10px 15px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &-logo {
            max-width: 35px;
            max-height: 35px;
        }

        &-submit {
            display: inline-block;
            position: relative;
            font-family: 'Lato', sans-serif;
            max-width: 100%;
            font-weight: 700;
            height: 35px;
            padding: 0 10px;
            background-color: #3498db;
            border-radius: 6px;
            border: none;
            font-size: 10px;
            color: #ffffff;
            text-align: center;
            letter-spacing: 1px;
            text-transform: uppercase;
            cursor: pointer;
        }
    }

    @include small-size-max {
        width: 340px;
        height: 420px;
    }

    @include extra-small-size-max {
        width: 300px;
        height: 400px;
    }

    &.email-popup__visible {
        visibility: visible;
        right: 10px;
        transition: all .5s ease-out;
    }
}