/* ------------------------------------------- */
/* GLOBAL CLASSES */
/* ------------------------------------------- */
.no-scroll {
    overflow-x: hidden !important;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}

.skewed {
    transform: skew(-10deg);
}

.active-lang {
    background-color: $c-pomegranate;
    color: $c-white !important;
}

.active-menu {
    
    @media screen and (max-width: 1024px) {
        display: block !important;
        position: relative !important;
    }   
}

.menu-burger__open {
    opacity: 1 !important;
}

.red-tag {
    display: inline-block;
    position: relative;
    z-index: 0;
    color: $c-white;

    &::before {
        content: '';
        position: absolute;
        width: calc(100% + 5px);
        height: 100%;
        left: 0;
        top: 0;
        background-color: $c-pomegranate;
        z-index: -1;
        transform: skewX(-6deg);
        outline: 1px solid transparent;
    }
}

/* DEFAULT BUTTON */
.btn {
    display: inline-block;
    position: relative;
    padding: 12px 50px;
    font-size: 20px;
    font-weight: 600;
    font-style: normal;
    text-transform: uppercase;
    z-index: 2;
    text-align: center;
    transition: color 250ms ease;
    max-width: 190px;
    color: $c-white;
    

    &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: $c-blue-light;
    z-index: -1;
    transform: skewX(-6deg);
    backface-visibility: hidden;
    background-clip: content-box;
    outline: 1px solid transparent;
    transition: background-color 250ms ease-in;
    }
} 

/* TEXT TRANSFORM */
.t-uppercase {
    text-transform: uppercase;
}

/* TEXT ALIGN */
.t-left {
    text-align: left;
}
.t-center {
    text-align: center;
}
.t-right {
    text-align: right;
}

/* FLEX ALIGN */
.f-start {
    align-items: flex-start;
}
.f-center {
    align-items: center;
}
.f-end {
    align-items: flex-end;
}

/* SECTION PADDING */
.section-padding {
    padding: 85px 0;

    @include extra-small-size-max  {
        padding: 50px 0;
    }
}

/* POSITION */
.absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}