/* ------------------------------------------- */
/* MORE INFO SECTION */
/* ------------------------------------------- */
.moreinfo-section {
    background-color: $c-porcelain;

    &__content {
        display: flex;
        justify-content: center;

        @include small-size-max {
            flex-direction: column;
            align-items: center;
        }
    }

    &__link {

        &-left,
        &-right {
            height: 350px;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            transform: skewX(-7deg);
            width: 50%;
            max-width: 610px;
            padding: 30px;
            overflow: hidden;
            outline: 1px solid transparent;
            z-index: 0;

            &::before {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                background-image: linear-gradient(180deg, rgba($c-blue-grey, .2) 30%, $c-blue-grey 100%);
                z-index: 1;
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                background-color: $c-pomegranate;
                transition: opacity .2s ease-in;
                opacity: 0;
                z-index: 2;
            }
            
            &:hover {
                z-index: 1;
                
                &::after {
                    opacity: .7;
                }
            }

            @include medium-size-max {
                height: 250px;
                width: 50%;
                max-width: 50%;
            }

            @include small-size-max {
                width: 65%;
                max-width: 65%;
            }

            @include extra-small-size-max {
                height: 170px;
                padding: 10px 15px;
                width: calc(100% - 50px);
                max-width: calc(100% - 50px);
            }
        }

        &-right {
            margin-top: 45px;
            margin-left: -100px;

            @include small-size-max {
                margin-top: 30px;
                margin-left: 0;
            }

            @include extra-small-size-max {
                margin-top: 20px;
            }
        }

        &-img {
            position: absolute;
            left: -30px;
            top: 0;
            height: 100%;
            object-fit: cover;
            object-position: center;
            min-width: calc(100% + 100px);
            width: calc(100% + 30px);
            transform: skewX(7deg);
        }

        &-content {
            transform: skewX(7deg);
            position: relative;
            z-index: 3;
        }

        &-title {
            @include medium-size-max {
                font-size: 22px;
                line-height: 42px;
            }

            @include extra-small-phone-size-max {
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &-text {
            position: relative;
            display: inline-block;

            &::after {
                content: '\f061';
                font-family: 'FontAwesome';
                display: inline;
                margin-left: 20px;
                font-style: normal;
                font-weight: 400;
                font-variant: normal;
                text-transform: none;
                text-rendering: auto;
                line-height: 1;
                transition: margin-left .2s ease-out;
            }

            &:hover {
                &::after {
                    margin-left: 25px;
                }
            }

            @include medium-size-max {
                font-size: 16px;
                line-height: 26px;
            }
        }
    }
}