/* ------------------------------------------- */
/* PARTNERS SECTION */
/* ------------------------------------------- */
.partners-section {

    &__content {

        &-list {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            @include medium-size-max {
                flex-wrap: wrap;
                justify-content: center;
            }
        }

        &-item {
            
            @include medium-size-max {
                display: flex;
                align-items: center;
                justify-content: center;
                min-width: 240px;
                max-width: 250px;
                padding: 10px;
                max-height: 80px;
            }

            @include small-size-max {
                min-width: 180px;
                max-width: 200px;
                width: 100%;
                padding: 10px 20px;
            }

            @include extra-small-size-max {
                min-width: 140px;
                max-width: 150px;
            }

            @include extra-small-phone-size-max {
                padding: 0;
                min-width: 120px;
                max-width: 130px;
            }
        }
    }
}