/* ------------------------------------------- */
/* SOCIAL SECTION */
/* ------------------------------------------- */
.social-section {
    background-image: url('../images/social-section-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        &-left {

            @include medium-size-max {
                margin-right: 30px;
            }

            @include small-size-max {
                margin-right: 0;
                margin-bottom: 30px;
            }
        }

        &-title {
            margin-bottom: 15px;

            @include small-size-max {
                text-align: center;
            }
        }

        &-text {
            @include small-size-max {
                text-align: center;
            }
        }

        &-list {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &-link {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 65px;
            height: 65px;
            border: 3px solid $c-white;
            color: $c-white;
            font-size: 28px;
            border-radius: 100%;
            margin-right: 20px;
            transition: all .3s ease-out;

            &:hover {
                border-color: $c-pomegranate;
                background-color: $c-pomegranate;
                transition: all .3s ease-out;
            }

            &:focus {
                border-color: $c-pomegranate;
                background-color: $c-pomegranate;
            }

            @include extra-small-phone-size-max {
                margin-right: 15px;
            }
        }

        @include large-size-max {
            padding: 0 15px;
        }

        @include small-size-max {
            flex-direction: column;
        }

        @include extra-small-phone-size-max {
            padding: 0;
        }
    }

    @media screen and (max-width: 370px) {
        background-image: url('../images/social-section-bg-phone.png');
    }
}