/* ------------------------------------------- */
/* FOOTER */
/* ------------------------------------------- */
.footer {
    padding-top: 100px;
    background-color: $c-blue-dark;
    font-family: $f-roboto-slab;
    font-size: 15px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 26px;
    color: rgba($color: $c-white, $alpha: .6);

    .container {
        @include large-size-max {
            max-width: 1000px;
        }

        @include medium-size-max {
            max-width: 800px;
        }

        @include small-size-max {
            max-width: 560px;
            padding: 0 30px;
        }
    }

    &__top {
        display: flex;
        margin-bottom: 50px;

        &-left {
            max-width: 250px;

            &__logo {
                .footer-logo {
                    max-width: 220px;
                    margin-bottom: 15px;
                }
            }

            &__text {
                margin-bottom: 20px;

                @include medium-size-max {
                    padding: 0 20px;
                }
            }

            .footer__contact {
                margin-bottom: 20px;

                &-location,
                &-phone {
                    display: flex;
                    color: rgba($color: $c-white, $alpha: .6);

                    i {
                        font-size: 20px;
                        color: $c-white;
                        margin-right: 10px;
                        top: 20px;
                    }

                    span {
                        color: rgba($color: $c-white, $alpha: .6);
                        transition: color .4s ease-in-out;

                        &:hover {
                            color: $c-white;
                            transition: color .4s ease-in-out;
                        }
                    }
                }

                &-location {
                    max-width: 200px;
                    margin-bottom: 20px;

                    i {
                        margin-top: 10px;
                        margin-right: 15px;
                    }
                }

                @include small-size-max {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                }
            }

            &__pay {
                display: flex;

                .pay-item {
                    margin-right: 10px;
                }
            }

            @include medium-size-max {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;
                max-width: 100%;
            }
        }

        &-right {
            margin-left: auto;
            display: flex;
            margin-top: 15px;

            .footer__nav {
                padding-left: 55px;

                &-header {
                    pointer-events: none;

                    &__title {

                        @include small-size-max {
                            font-size: 24px;

                            &::after {
                                content: "\f107";
                                position: absolute;
                                font-family: 'FontAwesome';
                                font-style: normal;
                                font-size: 20px;
                                color: rgba($color: $c-white, $alpha: .9);
                                top: 50%;
                                right: 0;
                                transform: translateY(-50%);
                            }
                        }
                    }

                    @include small-size-max {
                        position: relative;
                        padding: 10px 0 10px 5px;
                        text-align: left;
                        pointer-events: all;
                        cursor: pointer;
                    }
                }

                &-content {
                    display: block;
                    margin-top: 30px;

                    &__list {

                        @include small-size-max {
                            margin-bottom: 20px;
                            text-align: center;
                        }
                    }

                    &__link {
                        max-width: 210px;
                        color: rgba($color: $c-white, $alpha: .6);

                        &:hover {
                            color: $c-white;
                        }

                        span.red-tag {
                            font-family: $f-oswald;
                            padding-left: 5px;
                        }
                    }

                    &__doublelist {
                        display: flex;

                        .footer__nav-content__list:first-child {
                            margin-right: 40px;
                        }

                        @include small-size-max {
                            justify-content: space-between;
                        }
                    }

                    .footer__nav-content__link.active-f-link {
                        color: $c-white;
                    }

                    @include medium-size-min {
                        display: block !important;
                    }
                    
                    @include small-size-max {
                        display: none;
                        margin-top: 10px;
                    }
                }

                @include large-size-max {
                    padding-left: 30px;
                }

                @include medium-size-max {

                    &:first-child {
                        padding-left: 0;
                    }
                }

                @include small-size-max {
                    width: 100%;
                    padding-left: 0;
                    border-bottom: 1px solid rgba($color: $c-grey-light, $alpha: .1);
                }
            }

            @include extra-large-size-medium {
                margin-left: 80px;
            }

            @media screen and (max-width: 1199px){
                margin-left: 40px;
            }

            @include medium-size-max {
                margin-top: 50px;
                margin-left: 0;
            }

            @include small-size-max {
                width: 100%;
                flex-wrap: wrap;
                justify-content: flex-start;
            }
        }

        @include medium-size-max {
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    &__bottom {
        padding: 50px 0;
        font-size: 14px;
        line-height: 23px;
        border-top: 1px solid rgba($c-white, .1);

        @include extra-large-size-medium {
            padding: 50px 30px;
        }

        @include small-size-max {
            border: none;
        }

        @include small-size-max {
            padding: 0 30px 30px 30px;
        }
    }

    &__copyright {
        padding: 30px 0;
        font-size: 14px;
        line-height: 23px;
        background-color: #0f1926;

        &-text {
            display: block;
        }

        @include extra-small-size-max {
            padding: 20px 30px;
        }
    }

    @include small-size-max {
        padding-top: 50px;
    }
}