/* ------------------------------------------- */
/* SATISFIED CUSTOMERS SECTION */
/* ------------------------------------------- */
.satisfied-section {
    background-image: url('../images/flag-bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include small-size-max {
        background-image: url('../images/flag-bg-mobile.png');
        background-position: bottom;
    }

    &__content {

        &-title {
            margin-bottom: 55px;
        }
        
        &-box {
            max-width: 720px;
            margin: 0 auto;
            margin-bottom: 55px;
        }

        &-text {
            font-family: $f-roboto-slab;
            font-size: 18px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
        }

        &-text + &-text {
            margin-top: 30px;
        }

        &-btn {
            display: block;
            margin: 0 auto;
            height: 60px;
            padding: 18px 42px;
            color: $c-blue-dark;

            &::before {
                background-color: $c-white;
            }

            &:hover {
                &::before {
                    background-color: $c-athens-grey;
                }
            }

            &:active {
                &::before {
                    background-color: $c-athens-grey;
                }
            }

            @include lowmedium-size-max {
                padding: 17px 28px;
            }
        }
    }
}