.category-section {
    overflow-x: hidden;

    &__title {
        margin-bottom: 70px;
    }

    &__headers {
        color: $c-blue-dark;
    }

    &__slider {
        
        .category-swiper-container {
            position: relative;
            width: 100%;
            height: 100%;
            padding-bottom: 20px;

            .swiper-pagination {

                span {
                    width: 10px;
                    height: 9px;
                    border-radius: 0;
                    transform: skew(-7deg);
                    background-color: rgba($color: $c-blue-grey, $alpha: .8);
                }

                .swiper-pagination-bullet-active {
                    width: 20px;
                    height: 18px;
                    box-shadow: 2px 2px 0 rgba(45, 63, 89, 0.1);
                    background-color: $c-blue-grey;
                }

                @include large-size-max {
                    left: 3%;
                }
                
                @include medium-size-max {
                    left: 4%;
                }
                
                @include small-size-max {
                    left: 6%;
                }
                
                @include extra-small-size-max {
                    left: 11%;
                }

                @media (max-width: 420px) {
                    left: 15%;
                }
            }

            .swiper-button {
                width: 67px;
                height: 60px;
                font-style: normal;
                z-index: 6;
                
                &::after {
                    font-size: 26px;
                    color: $c-blue-dark;
                    opacity: 1;
                }
                
                &::before {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background-color: $c-porcelain;
                    opacity: .8;
                    transform: skewX(-7deg);
                    transition: opacity 250ms ease-in;
                    z-index: -1;
                }
            
                &:hover {
                    &::after {
                        color: $c-pomegranate;
                        z-index: 4;
                    }
            
                    &::before {
                        opacity: 1;
                        z-index: 4;
                    }
                }

                &-prev {
                    left: 80px;
                    z-index: 3;

                    @include extra-large-size-max {
                        left: 9%;
                    }
                    
                    @include extra-large-size-medium {
                        left: 11%;
                    }
                    
                    @include large-size-max {
                        left: 13%;
                    }

                    @media screen and (max-width: 1024px) {
                        display: none;
                    }
                }

                &-next {
                    right: 80px;
                    z-index: 3;

                    @include extra-large-size-max {
                        right: 6%;
                    }
            
                    @media screen and (max-width: 1024px) {
                        display: none;
                    }
                }
            }           

            .swiper-slide {
                max-width: 380px;
                transform: scale(0.7);
                transition: all .2s ease-out;

                &-prev {
                    transform: scale(0.9);
                    transition: all .2s ease-out;
                }

                &-next {
                    transform: scale(0.9);
                    transition: all .2s ease-out;
                }

                &__title {
                    color: $c-blue-dark;
                    font-size: 28px;
                    font-weight: 500;
                    line-height: 42px;
                    letter-spacing: normal;
                    text-align: center;
                    margin-bottom: 10px;
                }

                &__content {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    max-height: 260px;
                    height: 240px;
                    width: 100%;

                    &::before {
                        content: '';
                        position: absolute;
                        width: 80%;
                        height: 90%;
                        top: 0;
                        background-color: $c-blue-grey;
                        z-index: -1;
                        transform: skewX(-6deg);
                        outline: 1px solid transparent;
                        opacity: .1;

                        @include extra-large-size-medium {
                            height: 80%;
                        }
                    }

                    &-btn {
                        display: none;
                        position: absolute;
                        top: 70%;
                        left: 20%;
                        width: 190px;
                        height: 50px;
                        font-size: 20px;
                        font-weight: 600;
                        letter-spacing: normal;
                        line-height: 26px;
                        text-align: center;
                        text-transform: uppercase;
                        padding: 12px 28px;
                        z-index: 2;
                        
                        &::before {
                            box-shadow: 2px 2px 0 $c-white;
                            background-color: $c-pomegranate;
                        }

                        &:hover {
                            &::before {
                                background-color: #ff4353;
                            }
                        }

                        &:active {
                            &::before {
                                background-color: #ff4353;
                            }
                        }

                        @include extra-small-size-max {
                            padding: 10px 20px;
                        }
                    }

                    &-img {
                        position: absolute;
                        width: 100%;
                        max-height: 280px;
                        transform: translateY(5%);
                    }
                }

                &-active {
                    transform: scale(1.1);
                    top: -10px;
                    width: 100% !important;
                    transition: all .4 ease-in;
                    
                    .swiper-slide__title {
                        font-size: 35px;
                        color: $c-pomegranate;

                        @include extra-small-size-max {
                            font-size: 28px;
                        }
                    }

                    .swiper-slide__content-btn {
                        display: block;

                        @media (max-width: 400px) {
                            left: 17%;
                        }
                    }

                    .swiper-slide__content-img {
                        transform: translateY(8%);
                    }

                    .swiper-slide__content {
                        min-height: 340px !important;
                        min-width: 100% !important;

                        &::before {
                            content: '';
                            position: absolute;
                            width: 80%;
                            height: 80%;
                            top: 0;
                            background-color: transparent;
                            z-index: -2;
                            transform: skewX(-6deg);
                            backface-visibility: hidden;
                            outline: 1px solid transparent;
                            opacity: 1;
                            box-shadow: 2px 2px 0 $c-white;
                            border: 5px solid $c-pomegranate;
                        }

                        @include extra-small-size-max {
                            min-width: 88% !important;
                        }

                        @media (max-width: 420px) {
                            min-width: 60% !important;
                        }

                        @media (max-width: 400px) {
                            min-width: 90% !important;
                        }
                    }

                    @include extra-small-size-max {
                        width: 60% !important;
                    }

                    @media (max-width: 420px) {
                        width: 56% !important;
                    }

                    @media (max-width: 400px) {
                        width: 65% !important;
                    }
                }
            }
        }

        @media (max-width: 1680px) {
            margin-left: -20px;
        }

        @include extra-large-size-max {
            margin-left: -60px;
        }

        @include extra-large-size-medium {
            margin-left: -90px;
        }

        @include large-size-max {
            margin-left: -100px;
        }

        @include small-size-max {
            margin-left: -150px;
        }
    }
}