/* ------------------------------------------- */
/* SEARCH SECTION */
/* ------------------------------------------- */
.reviews-section {

    &__content {
        margin-bottom: 70px;

        &-title {
            color: $c-blue-dark;
        }
    }

    &__slider {
        position: relative;
        margin-bottom: 80px;

        @include extra-large-size-medium {
            padding: 0 40px;
        }
        
        .reviws-swiper-container {
            overflow: hidden;
        }
        
        .swiper {
            &-slide {
                
                .review-block {
                    display: flex;
                    flex-direction: column;

                    &__top {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 18px;

                        &-times {
                            font-size: 14px;
                            font-family: $f-roboto-slab;
                            color: $c-grey;

                            @include extra-small-phone-size-max {
                                margin-left: 5px;
                            }
                        }

                        .rating__group {
                            display: flex;
                            justify-content: space-between;
                            width: 118px;



                            &-star {
                                position: relative;
                                display: block;
                                width: 22px;
                                height: 22px;
                                background-color: $c-grey-medium;

                                &::before {
                                    content: '';
                                    display: block;
                                    position: absolute;
                                    width: 16px;
                                    height: 16px;
                                    left: 50%;
                                    top: 50%;
                                    transform: translate(-50%,-50%);
                                    background-image: url(../images/star.svg);
                                    background-size: 16px 16px;
                                }
                            }
                            
                            .rating__group-star.checked {
                                background-color: $c-jade;
                            }

                            .rating__group-star.bg-1 {
                                background-color: $c-bright-red;
                            }

                            .rating__group-star.bg-2 {
                                background-color: $c-orange;
                            }

                            .rating__group-star.bg-3 {
                                background-color: $c-supernova;
                            }

                            .rating__group-star.bg-4 {
                                background-color: $c-green-ligth;
                            }

                            .rating__group-star.bg-5 {
                                background-color: $c-jade;
                            }
                        }

                        @include extra-small-phone-size-max {
                            flex-wrap: wrap;
                        }
                    }

                    &__bottom {
                        color: $c-blue-dark;
                        
                        &-title {
                            font-size: 24px;
                            font-weight: 500;
                            font-style: normal;
                            line-height: 32px;
                            margin-bottom: 30px;

                            @include extra-large-size-medium {
                                white-space: nowrap;
                                overflow: hidden;
                                text-overflow: ellipsis;
                            }
                        }

                        &-text {
                            color: $c-grey;
                            font-family: $f-roboto-slab;
                            font-size: 15px;
                            font-weight: 400;
                            line-height: 26px;
                            margin-bottom: 30px;
                        }

                        &-author {
                            color: $c-blue-dark;
                            font-size: 18px;
                            font-weight: 500;
                            line-height: 28px;
                        }
                    }
                }
            }
        }

        .swiper-button {
            color: $c-blue-dark;

            &:active {
                color: $c-pomegranate;
            }

            &:focus {
                color: $c-pomegranate;
            }

            &::after {
                font-size: 30px;
            }
        }

        .swiper-button-prev {
            left: -105px;

            @include extra-large-size-max {
                left: -75px;
            }

            @include extra-large-size-medium {
                left: -25px;
            }

            @media screen and (max-width: 1300px) {
                left: -5px;
            }

            @media screen and (max-width: 1024px) {
                display: none;
            }
        }

        .swiper-button-next {
            right: -105px;

            @include extra-large-size-max {
                right: -75px;
            }

            @include extra-large-size-medium {
                right: -25px;
            }
            
            @media screen and (max-width: 1300px) {
                right: 0;
            }

            @media screen and (max-width: 1024px) {
                display: none;
            }
        }

        .swiper-pagination {
            bottom: -80px;

            span {
                width: 10px;
                height: 9px;
                border-radius: 0;
                transform: skew(-7deg);
                background-color: rgba($color: $c-blue-grey, $alpha: .5);
            }
            
            .swiper-pagination-bullet-active {
                width: 20px;
                height: 18px;
                box-shadow: 2px 2px 0 rgba(45, 63, 89, 0.1);
                background-color: $c-blue-grey;
            }
        }
    }
}