/* ------------------------------------------- */
/* HEADER */
/* ------------------------------------------- */
.header {
    background-color: $c-blue-main;

    &__container {
        max-width: 1750px;
        padding: 0 15px;
        margin: 0 auto;
        display: flex;

        @include medium-size-max {
            padding: 0 8px;
        }

        @media screen and (max-width: 1024px) { 
            padding: 0;
        }
    }

    &__left {
        display: flex;
        align-items: center;

        @include large-size-max {
            display: none;      
        }
    }

    &__right {
        margin-left: auto;

        &-top {
            display: flex;
            justify-content: flex-end;
            position: relative;
            margin-bottom: 20px;
            
            .header__topbar {
                display: flex;
                position: relative;
                
                &::before {
                    content: '';
                    position: absolute;
                    background-color: $c-pomegranate;
                    transform: skew(-10deg);
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                }

                &-lang {
                    position: relative;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 7px 13px;
                    background-color: #182a42;
                    z-index: 1;
                    cursor: pointer;

                    &::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 2px;
                        top: 100%;
                        left: 0;
                        opacity: 0;
                        z-index: 1;
                    }

                    &__general {
                        transform: skew(10deg);
                        display: flex;
                        justify-content: space-between;
                        align-items: center;

                        span {
                            padding: 0 5px;
                        }
                    }

                    &:hover .lang__dropdown {
                        visibility: visible;
                        opacity: 1;
                        
                        @media screen and (max-width: 1024px) {
                            opacity: 0;
                            visibility: hidden;
                        }
                    }

                    .lang__dropdown {
                        visibility: hidden;
                        opacity: 0;
                        position: absolute;
                        top: calc(100% + 2px);
                        left: 0;
                        background-color: $c-white;
                        transform: skew(10deg);
                        z-index: 20;
                        transition: visibility 0s, .25s ease-out;

                        &::before {
                            content: '';
                            position: absolute;
                            left: 50%;
                            top: -20px;
                            transform: translateX(-50%);
                            border: 10px solid transparent;
                            border-bottom: 10px solid $c-white;
                        }
                        
                        &-link {
                            font-family: $f-roboto-slab;
                            font-style: normal;
                            font-size: 14px;
                            line-height: 23px;
                            color: $c-grey;
                            text-transform: none;
                            padding: 11px 15px;
                            display: flex;
                            min-width: 110px;

                            img {
                                margin-right: 7px;
                            }

                            &:hover {
                                background-color: $c-pomegranate;
                                color: $c-white;
                            }
                        }

                        &.active-dropdown {
                            visibility: visible;
                            opacity: 1;
                        }

                        @media screen and (max-width: 1024px) {
                            left: 20%;
                        }
                    }

                    @media screen and (max-width: 1024px) {
                        margin-left: -3px;
                    }

                    @include extra-small-phone-size-max {
                        width: 79px;
                        padding: 5px 10px;
                    }
                }

                &-locations {
                    position: relative;
                    z-index: 1;

                    &::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 2px;
                        top: 100%;
                        left: 0;
                        opacity: 0;
                        z-index: 1;
                    }
                    
                    .topbar__locations {                        
                        display: flex;
                        position: relative;
                        height: 100%;
                        justify-content: center;
                        align-items: center;
                        padding: 6px 30px 6px 16px;

                        &::after {
                            content: '\f0d7';
                            position: absolute;
                            font-family: 'FontAwesome';
                            font-style: normal;
                            right: 10px;
                            z-index: -1;

                            @include extra-small-phone-size-max {
                                content: none;
                            }
                        }

                        &-location {
                            padding-left: 3px;
                            text-decoration: underline;
                            cursor: pointer;

                            &:hover {
                                color: $c-blue-main;
                            }

                            @include extra-small-phone-size-max {
                                display: none;
                            }
                        }

                        &-text {
                            cursor: pointer;

                            @include extra-small-size-max {
                                display: none;
                            }
                        }

                        i {
                            margin-right: 10px;

                            @include extra-small-phone-size-max {
                                margin-right: 5px;
                            }
                        }

                        &-dropdown {
                            visibility: hidden;
                            opacity: 0;
                            position: absolute;
                            top: calc(100% + 2px);
                            left: 50%;
                            background-color: $c-white;
                            min-width: calc(100% - 20px);
                            transform: translateX(-50%);
                            font-family: $f-roboto-slab;
                            font-size: 15px;
                            font-style: normal;
                            line-height: 26px;
                            color: $c-blue-main;
                            transition: visibility 0s, .25s ease-out;

                            &::before {
                                content: '';
                                position: absolute;
                                left: 50%;
                                top: -18px;
                                transform: translateX(-50%);
                                border: 8px solid transparent;
                                border-bottom: 10px solid #fff;
                            }

                            &__maps {
                                display: none;
                                padding: 20px 20px;
                                border-bottom: 1px solid $c-white-grey;

                                &-title {
                                    color: $c-blue-main;
                                    margin-bottom: 15px;
                                }

                                &-btn {
                                    min-width: 100%;
                                    font-family: $f-oswald;
                                    font-size: 16px;
                                    font-weight: 500;

                                    &::before {
                                        background-color: $c-pomegranate;
                                    }

                                    &:hover {

                                        &::before {
                                            background-color: #ff4d53;
                                        }
                                    }

                                    @include extra-small-phone-size-max {
                                        padding: 10px 40px;
                                    }
                                }

                                @include small-size-max {
                                    display: flex;
                                    flex-direction: column;
                                }
                            }

                            .locations__dropdown-list {
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                user-select: none;
                            }

                            .locations__dropdown-item {
                                border-bottom: 1px solid $c-white-grey;
                                cursor: pointer;
                            
                                &__location {
                                    padding: 10px 20px;
                                    transition: color .3s ease-in;
                                    position: relative;
                                    cursor: pointer;


                                    &::before {
                                        content: '+';
                                        position: absolute;
                                        font-style: normal;
                                        font-size: 21px;
                                        color: inherit;
                                        top: 50%;
                                        right: 20px;
                                        transform: translateY(-50%);
                                    }

                                    &:hover {
                                        color: $c-pomegranate;
                                        transition: color .3s ease-in;
                                    }
                                }

                                &__body {
                                    display: none;

                                    .dropdown-item__body-list {
                                        margin-bottom: 10px;
                                    }

                                    .dropdown-item__body-item {
                                        display: flex;
                                        padding: 4px 0 4px 30px;
                                        font-size: 14px;
                                        font-weight: 400;
                                        transition: background-color .3s ease-in;
                                        color: $c-blue-main;
                                        cursor: pointer;

                                        i {
                                            margin: 6px 8px 0 0;
                                            font-size: 14px;
                                            color: $c-blue-main;
                                            transition: color .3s ease-in;
                                        }
                                    }
                                }
                            }

                            .dropdown-item__body-item:hover {
                                background-color: rgba($c-pomegranate, .5);
                                color: $c-white-grey;
                                transition: color .3s ease-in, background-color .3s ease-in;

                                i {
                                    color: $c-white;
                                    transition: color .3s ease-in;
                                }
                            }

                            .locations__dropdown-item.open-location {
                                background-color: $c-white-grey;
                                transition: background-color .3s ease-in;


                                .locations__dropdown-item__location {
                                    &::before {
                                        content: '-';
                                        font-size: 30px;
                                    }
                                }
                            }

                            .locations__dropdown-item__body.active-location {
                                display: block;
                            }

                            .dropdown-item__body-item.active-location__link {
                                background-color: $c-pomegranate;
                                color: $c-white;
                                transition: color .3s ease-in, background-color .3s ease-in;

                                i {
                                    color: $c-white;
                                    transition: color .3s ease-in;
                                }
                            }
                            
                            @include small-size-max {
                                transform: translateX(-40%);
                                min-width: calc(100vw - 40px);
                            }

                            @include extra-small-phone-size-max {
                                transform: translateX(-43%);
                            }
                        }

                        &-mobiletext {
                            display: none;

                            @include extra-small-phone-size-max {
                                display: inline-block;
                            }
                        }

                        @media screen and (max-width: 1024px) {
                            cursor: pointer;
                        }

                        @include extra-small-phone-size-max {
                            padding: 6px 14px;
                        }
                    }

                    &:hover .topbar__locations-dropdown {
                        visibility: visible;
                        opacity: 1;

                        @media screen and (max-width: 1024px) {
                            visibility: hidden;
                            opacity: 0;
                        }
                    }

                    .topbar__locations-dropdown.active-dropdown {
                        visibility: visible;
                        opacity: 1;
                    }
                }

                &-phone {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding: 6px 33px 6px 17px;
                    z-index: 1;
                    position: relative;

                    &::before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        height: 100%;
                        width: 1px;
                        background-color: $c-white;
                        opacity: .15;
                        transform: rotate(7deg);
                    }

                    &__tel {
                        
                        &:hover .header__topbar-phone__number {
                            color: $c-blue-main;
                            transition: color 0.4s ease-out;
                        }
                    }

                    i {
                        margin-right: 10px;

                        @include extra-small-phone-size-max {
                            margin-right: 5px;
                        }
                    }

                    &__text {
                        @include extra-small-size-max {
                            display: none;
                        }
                    }

                    @include extra-small-phone-size-max {
                        padding: 6px 7px;
                    }
                }

                @include large-size-max {
                    height: 40px;
                }

                @media screen and (max-width: 1024px) { 
                    width: 100%;
                    justify-content: space-between;

                    &::before {
                        transform: none;
                    }
                }

                @include extra-small-phone-size-max {
                    font-size: 15px;
                }
            }

            &__logo {
                display: none;

                @include large-size-max {
                    display: block;
                    margin-top: 10px;
                    margin-right: auto;
                }

                @media screen and (max-width: 1024px) {
                    display: none;
                }
            }

            @include large-size-max {
                justify-content: space-between;
                margin-bottom: 15px;
            }

            @media screen and (max-width: 1024px) {
                justify-content: center;
                margin-bottom: 0;
            }
        }

        &-bottom {
            .header__navigation {
                display: flex;

                &-close {
                    display: none;
                    opacity: 0;
                    position: absolute;
                    width: 100%;
                    bottom: 0;
                    left: 0;
                    text-align: center;
                    font-weight: 500;
                    font-size: 18px;
                    padding: 14px 0;
                    background-color: $c-pomegranate;
                    transition: opacity 150ms ease-in;
                    cursor: pointer;

                    @media screen and (max-width: 1024px) {
                        display: block;
                        opacity: 1;
                    }                    
                }

                @media screen and (max-width: 1024px) {
                    opacity: 0;
                    position: absolute;
                    left: 0;
                    top: 100%;
                    background-color: $c-blue-dark;
                    width: 100%;
                    height: calc(100vh - 110px);
                    transition: opacity .4s ease-out;
                    z-index: 10;
                    pointer-events: none;
                    align-items: center;
                    justify-content: center;
                    z-index: 0;
                }

                @include extra-small-phone-size-max {
                    align-items: flex-start;
                }

                .header__navigation.menu-burger__open {
                    pointer-events: all;
                }
            }

            &__logo {
                display: none;

                @media screen and (max-width: 1024px) {
                    display: block;
                    width: 181px;
                }
            }
            
            .header__menu {
                display: flex;
                align-items: center;
                justify-content: space-between;
                
                &-list {
                    display: flex;

                    @media screen and (max-width: 1024px) {
                        flex-direction: column;
                        align-items: flex-start;
                        max-height: calc(100% - 80px);
                        width: 100%;
                        padding: 0 40px;
                        overflow: hidden;
                        overflow-y: auto;
                        margin-bottom: 55px;
                    }

                    @include small-size-max {
                        padding-top: 15px;
                    }
                }

                &-item {
                    &.active-link {
                        a {
                            &::before {
                                opacity: 1;
                            }
                        }
                    }

                    @media screen and (max-width: 1024px) {
                        width: 100%;
                        border-bottom: 1px solid rgba($color: $c-white, $alpha: .07);
                        
                        &:last-child {
                            border-bottom: none;
                        }
                    }
                }
                
                &-link {
                    display: block;
                    position: relative;
                    padding: 13px 20px;

                    &::after {
                        content: '';
                        position: absolute;
                        top: 0;
                        right: 0;
                        height: 100%;
                        width: 1px;
                        background-color: $c-white;
                        opacity: .15;
                        transform: rotate(7deg);
                        z-index: 0;
                    }

                    &::before {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        left: 0;
                        top: 0;
                        background-color: $c-pomegranate;
                        opacity: 0;
                        transform: skewX(-6deg);
                        transition: opacity .2s ease-in;
                        z-index: 0;
                    }

                    &:hover::before {
                        opacity: 1;
                    }

                    span {
                        position: relative;
                        font-size: 18px;

                        @include extra-large-size-medium {
                            font-size: 16px;
                        }

                        @media screen and (max-width: 1024px) {
                            font-size: 18px;
                            
                            &:hover {
                                color: $c-pomegranate;
                                transition: color .4s ease-out;
                            }
                        }
                    }
                    
                    .link__arrow-down {
                        display: none;
                        position: absolute;

                        @media screen and (max-width: 1024px) {
                            display: inline-block;
                            position: absolute;
                            right: -0;
                            top: 13px;
                            z-index: 2;
                            cursor: pointer;

                            &::before {
                                content: "\f078";
                                font-family: "FontAwesome";
                                font-style: normal;
                                font-size: 12px;
                                transform: translateY(-50%);
                                opacity: .5;
                            }
                        }
                    }

                    @include extra-large-size-max {
                        padding: 13px 16px;
                    }

                    @include extra-large-size-medium {
                        padding: 13px 9px;
                    }

                    @media screen and (max-width: 1024px) {

                        &::after,
                        &::before {
                            content: none;
                        }
                    }
                }
                
                .submenu {
                    display: none;
                    position: absolute;
                    width: 100vw;
                    left: 0;
                    padding: 55px 340px 70px 340px;
                    background-color: $c-blue-light;
                    box-shadow: inset 0 3px 0 $c-pomegranate;
                    z-index: 4;
                    
                    &__list {
                        display: flex;
                        justify-content: center;
                        flex-wrap: wrap;
                        padding: 0 10px;
                        
                        @include small-size-max {
                            padding: 0;
                        }
                        
                        @include extra-small-size-max {
                            min-width: 200px;
                            height: 210px;
                            overflow-y: scroll;
                            overflow-x: hidden;
                        }
                    }
                    
                    &__item {
                        max-width: 300px;
                        min-width: 270px;
                        width: 25%;
                        flex-basis: 25%;

                        @media (max-width: 1781px) {
                            min-width: 245px;
                        }

                        @include extra-large-size-max {
                            min-width: 230px;
                        }

                        @include extra-large-size-medium {
                            min-width: 230px;
                            max-width: 245px;
                        }

                        @include large-size-max {
                            min-width: 200px;
                            max-width: 220px;
                        }

                        @media screen and (max-width: 1024px) {
                            min-width: 160px;
                            max-width: 210px; 
                        }

                        @include small-size-max {
                            min-width: 110px;
                        }

                        @include extra-small-size-max {
                            min-width: 160px
                        }

                        @include extra-small-phone-size-max {
                            min-width: 220px;
                            max-width: 250px;
                        }
                    }

                    @media (max-width: 1781px) {
                        padding: 40px 260px 50px 260px;
                    }

                    @include extra-large-size-max {
                        padding: 40px 220px 50px 220px;
                    }

                    @include extra-large-size-medium {
                        padding: 10px 80px 10px 80px;
                    }

                    @include large-size-max {
                        padding: 20px 50px 10px 50px;
                    }

                    @media screen and (max-width: 1024px) {
                        padding: 0;
                        padding-top: 10px;
                        width: 100%;
                        background-color: transparent;
                    }
                    
                    &__link {
                        display: block;
                        padding: 13px 15px;
                        border: 2px solid transparent;
    
                        &:hover {
                            border-color:$c-pomegranate;
                            transition: all .4s ease-out;
                        }

                        img {
                            margin-bottom: 10px;
                        }

                        span {
                            font-size: 20px;
                            line-height: 26px;
                            font-weight: 500;
                            text-transform: none;

                            @include large-size-max {
                                font-size: 18px;
                            }

                            @include medium-size-max {
                                font-size: 16px;
                            }
                        }
                    }
                }
                
                .hover-menu:hover .submenu {
                    display: block;

                    @media screen and (max-width: 1024px) {
                        display: none;
                    }
                }

                &-burger {
                    display: none;
                    position: relative;
                    border: none;
                    background-color: transparent;
                    cursor: pointer;
                    z-index: 3;

                    .burger-frist, 
                    .burger-second, 
                    .burger-third {
                        width: 20px;
                        height: 2px;
                        background-color: $c-white;
                        margin: 4px 0;
                        transition: 0.4s;
                    }

                    @media screen and (max-width: 1024px) {
                        display: inline-block;
                    }
                }

                .active-burger .burger-frist {
                    transform: rotate(-45deg) translate(-4px, 4px);
                }
                  
                .active-burger .burger-second {
                    opacity: 0;
                }
                  
                .active-burger .burger-third {
                    transform: rotate(45deg) translate(-4px, -4px);
                }

                @include large-size-max {
                    justify-content: center; 
                }
                
                @media screen and (max-width: 1024px) {
                    flex-direction: row-reverse;
                }
            }

            .cart {
                position: relative;

                &__box {
                    position: relative;
                    display: block;
                    padding: 13px 20px;
                    
                    &:hover i {
                        color: $c-pomegranate;
                        transition: color 0.4s ease-out;
                    }

                    span {
                        position: absolute;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        width: 20px;
                        height: 20px;
                        background-color: $c-pomegranate;
                        border-radius: 100%;
                        font-size: 12px;
                        font-weight: 500;
                        padding: 1px 10px 2px 7px;
                        top: 8px;
                        right: 5px;

                        @include extra-large-size-max {
                            top: 5px;
                            right: 0;
                        }
                    }

                    @include extra-large-size-max {
                        padding: 13px 9px;
                    }
                }
                
                &:hover .cart__dropdown {
                    opacity: 1;
                    visibility: visible;
                }

                &__dropdown {
                    visibility: hidden;
                    position: absolute;
                    opacity: 0;
                    top: 43px;
                    right: -15px;
                    padding: 35px 35px 30px 35px;
                    min-width: 400px;
                    max-width: 470px;
                    width: 470px;
                    background-color: $c-blue-light;
                    border-top: 5px solid $c-pomegranate;
                    z-index: 20;
                    transition: visibility 0s, .25s ease-out;

                    &::before {
                        content: '';
                        position: absolute;
                        right: 20px;
                        top: -25px;
                        border: 10px solid transparent;
                        border-bottom: 10px solid $c-pomegranate;
                        z-index: 10;

                        @include extra-small-size-max {
                            content: none;
                        }
                    }

                    &-successfull {
                        color: $c-white;
                        font-size: 24px;
                        font-weight: 500;
                        line-height: 32px;
                        text-align: center;
                        padding-bottom: 30px;
                        border-bottom: 1px solid rgba($color: $c-white, $alpha: .2);

                        @include extra-small-size-max {
                            padding-bottom: 15px;
                        }
                    }

                    &-list {
                        margin-bottom: 30px;

                        li {
                            padding: 20px 0;
                            border-bottom: 1px solid rgba($color: $c-white, $alpha: .2);
                        }

                        @include extra-small-size-max {
                            margin-bottom: 10px;
                        }
                    }

                    &-item {
                        display: flex;

                        .cart__item {
                            &-images {
                                max-width: 110px;
                                width: 100%;
                                margin-right: 20px;
                            }
    
                            &-info {
                                font-family: $f-roboto-slab;
                                color: rgba($color: $c-white, $alpha: .6);
                                font-size: 14px;
                                font-weight: 400;
                                font-style: normal;
                                letter-spacing: normal;
                                line-height: 23px;

                                .cart-name {
                                    transition: color .4s ease-out;
                                }

                                .cart-category {
                                    margin-bottom: 10px;
                                }

                                .cart-price {
                                    color: $c-white;
                                    font-family: $f-oswald;
                                    font-size: 20px;
                                    font-weight: 500;
                                    line-height: 26px;
                                }
                            }
                        }

                        &:hover .cart-name {
                            color: $c-pomegranate;
                            transition: color .4s ease-out;
                        }
                    }

                    &-buttons {
                        display: flex;
                        justify-content: space-between;
                        padding: 0 3px;

                        &-btn {
                            &::before {
                                background-color: $c-pomegranate;
                            }

                            &:first-child {
                                padding: 12px 60px;
                                color: $c-pomegranate;
                                
                                &::before {
                                    background-color: $c-white;
                                }

                                &:hover {
                                    &::before {
                                        background-color: $c-athens-grey;
                                    }
                                }

                                &:active {
                                    &::before {
                                        background-color: $c-athens-grey;
                                    }
                                }

                                @include extra-small-size-max {
                                    padding: 12px 50px;
                                    margin-bottom: 10px;
                                }
                            }
                            
                            &:last-child {
                                &:hover {
                                    &::before {
                                        background-color: #ff4d53;
                                    }
                                }

                                &:active {
                                    &::before {
                                        background-color: #ff4d53;
                                    }
                                }

                                @include extra-small-size-max {
                                    padding: 12px 40px;
                                }
                            }

                            @include extra-small-size-max {
                                width: 100%;
                            }
                        }

                        @include extra-small-size-max {
                            flex-direction: column;
                            align-items: center;
                            justify-content: space-around;
                        }
                    }

                    @media screen and (max-width: 1024px) {
                        top: 58px;
                    }

                    @include small-size-max {
                        padding: 20px 25px 20px 25px;
                        width: 440px;
                    }

                    @include extra-small-size-max {
                        padding: 20px 8px 10px 8px;
                        width: 370px;
                        top: 52px;
                    }

                    @include extra-small-size-max {
                        padding: 20px 6px 10px 6px;
                        width: 100%;
                        max-width: 100%;
                        min-width: 100%;
                        top: 110px;
                        left: 0;
                    }
                }

                @media screen and (max-width: 1024px) {
                    margin-right: 10px;
                }

                @include extra-small-size-max {
                    position: static;
                }
            }

            @media screen and (max-width: 1024px) {
                display: flex;
                justify-content: space-between;
                padding: 12px 40px;
            }

            @include extra-small-phone-size-max {
                padding: 10px 10px;
            }
        }

        @include large-size-max {
            margin-left: 0;
            width: 100%;
        }
    }

    i {
        color: $c-white;
    }

    .header__navigation.menu-burger__open {
        pointer-events: all;
        overflow: hidden;
    }

    .header__navigation-scroll {
        position: fixed;
        width: 100%;
        left: 0;
        top: -200px;
        background-color: $c-blue-main;
        display: flex;
        justify-content: center;
        opacity: 0;
        transition: top 350ms linear, opacity .3s ease;
        z-index: 111;

        &.scroll-active {
            opacity: 1;
            top: 0;
        }

        .header__menu {
            display: flex;
            align-items: center;
            justify-content: space-between;
            
            &-list {
                display: flex;
            }

            &-item {

                &.active-link {
                    a {
                        &::before {
                            opacity: 1;
                        }
                    }
                }

                @media screen and (max-width: 1024px) {
                    width: 100%;
                    border-bottom: 1px solid rgba($color: $c-white, $alpha: .07);
                    
                    &:last-child {
                        border-bottom: none;
                    }
                }
            }
            
            &-link {
                display: block;
                position: relative;
                padding: 13px 20px;

                &::after {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    height: 100%;
                    width: 1px;
                    background-color: $c-white;
                    opacity: .15;
                    transform: rotate(7deg);
                    z-index: 0;
                }

                &::before {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background-color: $c-pomegranate;
                    opacity: 0;
                    transform: skewX(-6deg);
                    transition: opacity .2s ease-in;
                    z-index: 0;
                }

                &:hover::before {
                    opacity: 1;
                }

                span {
                    position: relative;
                    font-size: 16px;

                    @include extra-large-size-medium {
                        font-size: 14px;
                    }
                }
                
                .link__arrow-down {
                    display: none;
                    position: absolute;

                    @media screen and (max-width: 1024px) {
                        display: inline-block;
                        position: absolute;
                        right: -0;
                        top: 13px;
                        z-index: 2;
                        cursor: pointer;

                        &::before {
                            content: "\f078";
                            font-family: "FontAwesome";
                            font-style: normal;
                            font-size: 12px;
                            transform: translateY(-50%);
                            opacity: .5;
                        }
                    }
                }

                @include extra-large-size-max {
                    padding: 13px 16px;
                }

                @include extra-large-size-medium {
                    padding: 13px 9px;
                }
            }

            @include large-size-max {
                justify-content: center; 
            }
        }

        @media screen and (max-width: 1024px) {
            display: none;
        }

        .submenu {
            display: none;
            position: absolute;
            width: 100vw;
            left: 0;
            padding: 55px 340px 70px 340px;
            background-color: $c-blue-light;
            box-shadow: inset 0 3px 0 $c-pomegranate;
            z-index: 4;
            
            &__list {
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                padding: 0 10px;
                
                @include small-size-max {
                    padding: 0;
                }
            }
            
            &__item {
                max-width: 300px;
                min-width: 270px;
                width: 25%;
                flex-basis: 25%;

                @media (max-width: 1781px) {
                    min-width: 245px;
                }

                @include extra-large-size-max {
                    min-width: 230px;
                }

                @include extra-large-size-medium {
                    min-width: 230px;
                    max-width: 245px;
                }

                @include large-size-max {
                    min-width: 200px;
                    max-width: 220px;
                }
            }

            @media (max-width: 1781px) {
                padding: 40px 260px 50px 260px;
            }

            @include extra-large-size-max {
                padding: 40px 220px 50px 220px;
            }

            @include extra-large-size-medium {
                padding: 10px 80px 10px 80px;
            }

            @include large-size-max {
                padding: 20px 50px 10px 50px;
            }
            
            &__link {
                display: block;
                padding: 13px 15px;
                border: 2px solid transparent;

                &:hover {
                    border-color:$c-pomegranate;
                    transition: all .4s ease-out;
                }

                img {
                    margin-bottom: 10px;
                }

                span {
                    font-size: 20px;
                    line-height: 26px;
                    font-weight: 500;
                    text-transform: none;

                    @include large-size-max {
                        font-size: 18px;
                    }

                    @include medium-size-max {
                        font-size: 16px;
                    }
                }
            }
        }
        
        .hover-menu:hover .submenu {
            display: block;
        }
    }

    @media screen and (max-width: 1024px) {
        position: fixed;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 2000;
    }
}