/* ------------------------------------------- */
/* GLOBAL STYLES */
/* ------------------------------------------- */
div,
ul {
    box-sizing: border-box;
    outline: none;

    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: rgba($color: $c-pomegranate, $alpha: .8);
        box-shadow: inset 0 0 6px rgba($color: $c-black, $alpha: .5);
    }
    
    &::-webkit-scrollbar {
        width: 10px;
    }
    
    &::-webkit-scrollbar-track {
        border-radius: 10px;
        box-shadow: inset 0 0 6px rgba($color: $c-black, $alpha: .3);
    }
}

*::selection {
    color: $c-pomegranate;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    color: $c-white;
    font-style: normal;
    font-family: $content-font-family;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @media screen and (min-width: 1024px) {
        overflow: auto !important;
    }

    @media screen and (max-width: 1024px) {
        padding-top: 110px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
blockquote,
table,
embed,
iframe,
object,
video {
    margin-top: 0;
    margin-bottom: 0;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: $headings-font-family;
    font-weight: $headings-font-weight;
    color: $headings-txt-color;
}

h1,
.h1 {
    font-size: $h1-txt-size;

    @include extra-small-size-max {
        font-size: $h1-mob-txt-size;
    }
}

h2,
.h2 {
    font-size: $h2-txt-size;

    @include extra-small-size-max {
        font-size: $h2-mob-txt-size;
    }
}

h3,
.h3 {
    font-size: $h3-txt-size;

    @include small-size-max {
        font-size: $h3-mob-txt-size;
    }
}

h4,
.h4 {
    font-size: $h4-txt-size;

    @include small-size-max {
        font-size: $h4-mob-txt-size;
    }
}

h5,
.h5 {
    font-size: $h5-mob-txt-size;
}

h6,
.h6 {
    font-size: $h6-txt-size;

    @include small-size-max {
        font-size: $h6-mob-txt-size;
    }
}

ul,
ol {
    padding: 0;

    ul,
    ol {
        margin: 0;
    }
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
}

a {
    text-decoration: none;
    color: $c-white;
    transition: color 0.4s ease-out;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

b,
strong {
    font-weight: bold;
}

button {
    font-style: normal;
    font-family: $f-oswald-bold;
}

embed,
iframe,
object,
video {
    width: 100%;
    max-width: 100%;
    vertical-align: middle;
    border: none;
}