/* ------------------------------------------- */
/* SEARCH SECTION */
/* ------------------------------------------- */
.search-section {
    position: relative;
    font-style: normal;

    .search-box {
        position: absolute;
        bottom: -25px;
        z-index: 1;
        width: 1170px;
        height: 120px;
        
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: -1;
            transform: skew(-7deg);
            outline: 1px solid transparent;
            box-shadow: 4px 4px 0 $c-white;
            border: 2px solid $c-white;
            background-color: $c-blue-light;
        }

        @include extra-large-size-medium {
            width: 1150px;
        }

        @include large-size-max {
            width: 90%;
            left: 5%;
        }

        @media screen and (max-width: 1024px) {
            position: relative;
            left: 0;
            bottom: 0;
            width: 100%;
            border: none;
            box-shadow: none;
            transform: none;

            &::before {
                content: none;
            }
        }
    }
    
    .search-form {
        height: 100%;
        width: 100%;
        display: flex;

        &__options {
            position: relative;
            display: flex;
            width: 100%;

            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                z-index: -1;
                transform: skew(-7deg);
                outline: 1px solid transparent;
            }

            @media screen and (max-width: 1024px) {
                flex-direction: column;
                align-items: center;
            }
        }

        &__filter {
            display: flex;
            flex-direction: column;
            width: 50%;
            padding: 17px 22px 22px 30px;

            &:first-child {
                padding-right: 0;

                @media screen and (max-width: 1024px) {
                    padding-right: 22px;
                }
            }

            &-title {
                margin-left: 28px;
                margin-bottom: 3px;

                @media screen and (max-width: 1024px) {
                    margin-left: 0;
                }
            }

            @media screen and (max-width: 1024px) {
                align-items: center;
                width: 100%;

                &:first-child {
                    padding-bottom: 5px;
                }
            }
        }

        &__btn {
            min-width: 275px;
            font-size: 24px;
            font-weight: 600;
            padding: 10px;
            font-family: $f-oswald;
            font-style: normal;
            text-transform: uppercase;
            background: transparent;
            border: none;
            cursor: pointer;

            &::before {
                transform: none;
                background-color: $c-pomegranate;
                transform: skew(-7deg);
                border: 2px solid $c-white;
                border-left: none;
            }

            &:hover {
                &::before {
                    background-color: #ff4d53;
                }
            }

            &:active {
                &::before {
                    background-color: #ff4d53;
                }
            }

            @include large-size-max {
                min-width: 220px;
            }

            @include lowmedium-size-max {
                min-width: 190px;
            }

            @media screen and (max-width: 1024px) {
                font-size: 20px;
                padding: 20px 22px;
                min-width: 520px;
                max-width: 540px;
                background-color: inherit;
                z-index: 0;
                
                &::before {
                    content: '';
                    transform: skew(-10deg);
                    width: 100%;
                    height: 100%;
                    box-shadow: 4px 4px 0 $c-white;
                    border: 2px solid $c-white;
                }
            }

            @include extra-small-size-max {
                min-width: 88%;
            }

            @include extra-small-phone-size-max {
                min-width: 83%;
            }
        }

        @media screen and (max-width: 1024px) {
            flex-direction: column;
            align-items: center;
        }
    }

    .select-wrapper {
        position: relative;
        width: 100%;
        max-width: 540px;
    }
    
    .select {
        position: relative;
        display: flex;
        flex-direction: column;
        border: none;
        z-index: 2;
    
        &__trigger {
            position: relative;
            height: 52px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 25px;
            font-size: 15px;
            font-weight: 400;
            color: $c-white;
            line-height: 26px;
            cursor: pointer;
            
            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                z-index: -1;
                transform: skew(-7deg);
                background: rgba($color: $c-white, $alpha: .1);
            }

            &-text span {
                margin-left: 10px;
            }

        }
    }

    .search-form__filter-select {
        position: absolute;
        display: block;
        top: calc(100% + 1px);
        left: 0;
        right: 0;
        border: none;
        border-top: 0;
        background: $c-white;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        transition: all 0.4s ease-out;
        z-index: 2;
        height: 220px;
        overflow-y: scroll;

        @include large-size-max {
            overflow-x: hidden;
            width: 100%;
        }
    }

    .select.open .search-form__filter-select {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    .select.open:first-child {
        z-index: 5;
    }

    .select-option {
        transform: none;
        position: relative;
        display: block;
        padding: 10px 0 10px 30px;
        font-size: 14px;
        font-family: $f-roboto-slab;
        font-weight: 400;
        line-height: 23px;
        color: $c-grey;
        transition: all 0.4s ease-out;
        cursor: pointer;
    
        &:hover {
            background-color: $c-pomegranate;
            color: $c-white;
            cursor: pointer;
        }
        
        &.selected {
            background-color: $c-pomegranate;
            color: $c-white;
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 30px;
            width: 335px;
            height: 1px;
            background-color: rgba(20, 33, 51, 0.07);

            @include large-size-max {
                width: 80%;
            }

            @media screen and (max-width: 1024px) {
                width: 90%;
            }

            @include extra-small-size-max {
                width: 85%;
            }

            @include extra-small-phone-size-max {
                width: 80%;
            }
        }

        &__title {
            font-family: $f-roboto-slab;
            font-size: 14px;
            font-weight: 600;
            color: $c-black;
            padding: 5px 15px;
            margin-top: 10px;
            pointer-events: none;
            cursor: default;
        }

        @include lowmedium-size-max {
            font-style: normal;
        }
    }

    .arrow {
        position: relative;
    }
    
    .arrow::after {
        content: '\f0d7';
        position: absolute;
        font-family: 'FontAwesome';
        font-style: normal;
        font-size: 18px;
        right: 25px;
        top: -10px;
        z-index: -1;
    }

    @media screen and (max-width: 1024px) {
        border-top: 2px solid $c-white;
        background-color: $c-blue-light;
        box-shadow: 4px 4px 0 $c-white;
        height: 400px;
        padding-top: 40px;
    }

    @include extra-small-size-max {
        padding-top: 30px;
        height: 360px;
    }
}