/* ------------------------------------------- */
/* RESET */
/* ------------------------------------------- */
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.margin-tb-reset {
  margin-top: 0;
  margin-bottom: 0;
}
.margin-rl-items-reset {
  &:first-child {
    margin-left: 0;
  }
  &:last-child {
    margin-right: 0;
  }
}
.margin-tb-items-reset {
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
.padding-rl-items-reset {
  &:first-child {
    padding-left: 0;
  }
  &:last-child {
    padding-right: 0;
  }
}
